<template>
  <!-- 集群 -->
  <div>
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" type="warning" size="small" @click="handleAdd">{{$t('common.add')}}</el-button>
    </div>
    <el-form label-width="110px" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="集群名称：">
            <el-input type="text" v-model.trim="clusterName" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" size="small" @click="search" icon="el-icon-search">
            {{$t('common.search')}}
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table
      stripe
      v-loading="loading"
      :data="redisCluster"
      style="width: 100%">
      <el-table-column
        align="center"
        prop="clusterName"
        :label="$t('redis.redisCluster.clusterName')"
        width="200">
      </el-table-column>
      <el-table-column
        align="center"
        :label="$t('redis.redisCluster.clusterDetail')"
        width="110">
        <template slot-scope="scope">
          <el-button type="text" size="mini" @click="handleClusterDetail(scope.row)"><i class="fa fa-tasks"></i>
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        :label="$t('redis.redisCluster.relationShip')"
        width="110">
        <template slot-scope="scope">
          <el-button type="text" size="mini" @click="handleMasterSlave(scope.row)"><i class="fa fa-cubes"></i> 查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="clusterDesc"
        :label="$t('redis.redisCluster.clusterDesc')">
      </el-table-column>
      <el-table-column
        :label="$t('common.operation')" width="190">
        <template slot-scope="scope">
          <el-button type="text" size="mini" icon="el-icon-edit" @click="handleEdit(scope.row)">编辑
          </el-button>
          <el-button type="text" class="btn-text-red" size="mini" icon="el-icon-delete"
                     @click="deleteRow(scope.row)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next, jumper"
        :total="totalPage">
      </el-pagination>
    </div>
    <!-- 添加弹出框 -->
    <el-dialog :title="way" :visible.sync="addVisible" @close="cancel()" width="40%" center>
      <el-form ref="listForm" :model="listForm" :rules="inputRules" status-icon label-width="100px" size="small">
        <el-row>
          <el-col :span="24">
            <el-form-item label="集群名称" prop="clusterName">
              <el-input v-model.trim="listForm.clusterName" maxlength="50" placeholder="请输入1-50位字符" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="描述">
              <el-input type="textarea" v-model.trim="listForm.clusterDesc" clearable maxlength="100" placeholder="请输入1-100位字符"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="saveAdd" size="small">确 定</el-button>
                <el-button @click="cancel()" size="small">取 消</el-button>
            </span>
    </el-dialog>
    <!-- 主从关系弹出框 -->
    <el-dialog :title="title" :visible.sync="showMasterSlave" width="40%" center>
      <el-table
        border stripe
        :data="masterSlave"
        style="width: 100%">
        <el-table-column
          align="center"
          prop="masterInstName"
          :label="$t('redis.masterSlave.masterInstName')">
        </el-table-column>
        <el-table-column
          align="center"
          prop="bindIp"
          :label="$t('redis.masterSlave.bindIp')"
          width="150">
        </el-table-column>
        <el-table-column
          align="center"
          prop="port"
          width="150"
          :label="$t('redis.masterSlave.port')">
        </el-table-column>
        <el-table-column
          align="center"
          prop="slaveInstName"
          :label="$t('redis.masterSlave.slaveInstName')">
        </el-table-column>
        <el-table-column
          align="center"
          prop="slaveInstIp"
          :label="$t('redis.masterSlave.slaveInstIp')"
          width="150">
        </el-table-column>
        <el-table-column
          align="center"
          prop="slaveInstPort"
          :label="$t('redis.masterSlave.slaveInstPort')"
          width="150">
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        currentPage: 1, // 当前页数
        totalPage: 10, // 总页数
        loading: true,
        title: '',
        pageSize: 10,
        clusterName: '',
        way: '', // 弹出框名字
        addVisible: false, // 弹出框确定
        showMasterSlave: false, // 主从关系
        // 查询规则
        inputRules: {
          clusterName: [{ required: true, message: '请输入集群名称', trigger: 'blur' }]
        },
        // 模板
        listForm: {
          clusterName: '',
          clusterDesc: ''
        }
      }
    },
    computed: {
      ...mapState({
        redisCluster: state => state.redis.redisCluster,
        masterSlave: state => state.redis.masterSlave
      })
    },
    mounted() {
      this.search()
    },
    methods: {
      ...mapActions('redis', ['findRedisCluster', 'addRedisCluster', 'deleteRedisCluster', 'updateRedisCluster', 'findRedisMasterToSlave']),
      // 翻页事件
      handleCurrentChange(val) {
        this.currentPage = val
        this.search()
      },
      // 查询
      search() {
        this.loading = true
        const vm = this
        vm.listForm.clusterName = vm.clusterName
        vm.clusterParam = Object.assign({
          start: vm.currentPage,
          length: vm.pageSize
        }, vm.listForm)
        vm.findRedisCluster(vm)
      },
      // 模板清空
      resetForm() {
        this.listForm = {
          clusterName: '',
          clusterDesc: ''
        }
      },
      // 添加
      handleAdd() {
        this.way = '添加redis集群'
        this.resetForm()
        this.addVisible = true
      },
      // 编辑一行
      handleEdit(row) {
        this.way = '修改redis集群'
        this.resetForm()
        this.listForm = {
          id: row.id,
          clusterName: row.clusterName,
          clusterDesc: row.clusterDesc
        }
        this.addVisible = true
      },
      // 添加/编辑-确定
      saveAdd() {
        const vm = this
        let {clusterName} = vm.listForm
        if (!clusterName) {
            this.$message({
              type: 'warning',
              message: '带*不能为空'
            })
            return
          }
        // 添加
        if (vm.way === '添加redis集群') {
          vm.addRedisCluster(vm)
        }
        // 编辑
        if (vm.way === '修改redis集群') {
          vm.updateRedisCluster(vm)
        }
        vm.addVisible = false
      },
      cancel() {
        this.$refs.listForm.resetFields()
        this.addVisible = false
      },
      // 删除一行
      deleteRow(row) {
        const vm = this
        vm.conmmonMethods.makePopBox(vm, '删除').then(() => {
          this.listForm.id = row.id
          vm.deleteRedisCluster(vm)
        })
      },
      // 查看主从关系
      handleMasterSlave(row) {
        const vm = this
        this.title = row.clusterName + ' 主从关系展现'
        this.clusterId = row.id
        this.findRedisMasterToSlave(vm)
        this.showMasterSlave = true
      },
      // 查看集群详情
      handleClusterDetail(row) {
        this.$router.push({
          name: 'redisList',
          params: {
            clusterId: row.id.toString()
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
</style>



